@import url('https://fonts.googleapis.com/css2?family=Cinzel&display=swap');

html, body {
  background-color: black;
  margin: 0;
  padding: 0;
}

h1 {
  color: white;
  font-family: 'Cinzel', serif;
}

header {
  display: flex;
  align-items: center;
  justify-content: center;
}

.harem {
  display: flex;
  background-color: black;
  text-align: center;
  min-height: 100vh;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  flex-direction: column;
  justify-content: center;
}

.harem-title,
.doctor-title {
  opacity: 0;
}

.doctor {
  position: relative;
  background-color: black;
  border-radius: calc(25vw + 0.5rem);
  border: 0.25rem solid white;
  cursor: pointer;
  width: 50vw;
  max-width: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
}

.doctor::before {
  font-family: 'Cinzel', serif;
  content: 'Enter';
  position: absolute;
}

.doctor img {
  border-radius: calc(50% + 2rem);
  width: 100%;
  opacity: 0;
}

.left-hoe, .right-hoe {
  height: 200px;
  opacity: 0;
}

.doctor-title, 
.harem-title, 
.doctor::before, 
.doctor {
  transition: opacity 4s;
}

.harem.ready .doctor::before, 
.harem.ready .doctor {
  opacity: 1;
}

.harem.loading .doctor {
  opacity: 1;
}
.harem.loading .doctor::before {
  opacity: 0;
}

.harem.loaded .doctor,
.harem.loaded .harem-title,
.harem.loaded .doctor-title {
  opacity: 1;
}
.harem.loaded .doctor::before {
  content: '';
}
.harem.loaded .doctor img, 
.harem.loaded .left-hoe, 
.harem.loaded .right-hoe {
  transition: 5s opacity;
  opacity: 1;
}

@media (prefers-reduced-motion: no-preference) {
  .harem.loaded .left-hoe {
    animation: left-hoe-bounce infinite 0.47s linear;
  }
  .harem.loaded .right-hoe {
    animation: right-hoe-bounce infinite 0.47s linear;
  }
  .harem.loaded .doctor {
    animation: spin infinite 9.40s linear;
  }
}

@keyframes left-hoe-bounce {
  0% {
    transform: scaleX(1);
  }
  50% {
    transform: scaleX(1.1) scaleY(1.05);
  }
  100% {
    transform: scaleX(1);
  }
}

@keyframes right-hoe-bounce {
  0% {
    transform: scaleX(-1);
  }
  50% {
    transform: scaleX(-1.1) scaleY(1.05);
  }
  100% {
    transform: scaleX(-1);
  }
}

@keyframes spin {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}